import React from 'react'
import { Button, Modal, Input, Icon } from 'antd'
import Common from '/page/common.jsx'

import SupplierForm from '/page/supplier/supplierForm.jsx'

class Project extends Common{

	async componentWillMount () {
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '合同号',
			dataIndex: 't1'
		},{
			title: '项目编码',
			dataIndex: 't2'
		},{
			title: '项目名称',
			dataIndex: 't3'
		},{
			title: '合同名称',
			dataIndex: 't4'
		},{
			title: '合同金额',
			dataIndex: 't5'
		},{
			title: '起始日期',
			dataIndex: 't6'
		},{
			title: '结束日期',
			dataIndex: 't7'
		},{
			title: '签订日期',
			dataIndex: 't8'
		}],
		modalConf: {visible: false, item: {}}
	})

	search = async _ => {
		let data = [{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'},
		{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'},
		{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'},
		{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'},
		{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'},
		{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'},
		{key: 1, t1: 'HT112110915', t2: '102081211069', t3: '大同华林有限责任公司IBM小型机设备销售', t4: '大同华林有限责任公司IBM小型机设备销售合同', t5: '32000.00', t6: '2019-01-02', t7: '2019-01-02', t8: '2019-01-01'}]
		this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: 7,
					current: 0
				})
			})
	}


	delete = _ => {
		this.handleOk(deletesupplier, 'id', '删除')
	}

	done = async _ => {
		let modalConf = {visible: false, item: {}}
		await this.setState({ modalConf })
		this.research()
	}

	renderSearch = _ => <div className="mgrSearchBar">
		<Input 
		value={this.state.search.name}
		suffix={this.state.search.name ? <Icon 
		type="close-circle" 
		onClick={_ => this.changeSearch({name: ''})} /> : null}
		onChange={e => this.changeSearch({name: e.target.value})}
		style={{width: 250}}
		addonBefore="名称" placeholder="名称" />
		<Button 
		onClick={this.search}
		type="primary" icon="search">搜索</Button>
		<Button 
		onClick={this.research}
		type="primary" icon="retweet">重置</Button>
		<Button 
		onClick={_ => this.addmodal('modalConf', '同步')}
		type="primary" icon="sync">同步</Button>
		<Button 
		onClick={_ => this.addpanel}
		type="primary" icon="eye">查看</Button> 
	</div>


	rendermodal = _ => [<SupplierForm 
		onCancel={_ => this.cancelform('modalConf')}
		done={this.done}
		config={this.state.modalConf} />,
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="确认"
		  cancelText="取消"
		>
			<p>是否删除？</p>
		</Modal>]

}

export default Project


